"use client";

import { useState } from "react";
import { motion, AnimatePresence } from "motion/react";
import { useNavLinks } from "@/lib/nav-links";
import { cn } from "@/lib/utils";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { ChevronLeft, ChevronRight } from "lucide-react";

export function SidebarNav() {
  const [open, setOpen] = useState(true);
  const navLinks = useNavLinks();
  const pathname = usePathname();

  return (
    <motion.aside
      initial={false}
      animate={{
        width: open ? 280 : 80,
      }}
      transition={{
        duration: 0.3,
        ease: [0.22, 1, 0.36, 1],
      }}
      className={cn(
        "fixed left-0 top-0 z-40 h-screen border-r border-border/70 bg-surface/90 backdrop-blur-sm",
        "flex flex-col"
      )}
    >
      <div className="flex h-16 items-center justify-between border-b border-border/70 px-4">
        <AnimatePresence mode="wait">
          {open && (
            <motion.div
              initial={{ opacity: 0, x: -10 }}
              animate={{ opacity: 1, x: 0 }}
              exit={{ opacity: 0, x: -10 }}
              transition={{ duration: 0.2 }}
              className="text-lg font-semibold text-text-primary"
            >
              K2 Lab
            </motion.div>
          )}
        </AnimatePresence>
        <button
          onClick={() => setOpen(!open)}
          className="flex h-8 w-8 items-center justify-center rounded-lg border border-border/70 bg-surface-muted/70 text-text-secondary transition-colors hover:bg-surface-hover hover:text-neon-green"
        >
          {open ? (
            <ChevronLeft className="h-4 w-4" />
          ) : (
            <ChevronRight className="h-4 w-4" />
          )}
        </button>
      </div>

      <nav className="flex-1 overflow-y-auto p-4">
        <ul className="space-y-2">
          {navLinks.map((link) => {
            const isActive = pathname === link.href;
            return (
              <li key={link.href}>
                <Link
                  href={link.href}
                  className={cn(
                    "group flex items-center gap-3 rounded-lg px-3 py-2.5 text-sm font-medium transition-all duration-200",
                    "hover:bg-surface-hover/70 hover:text-neon-green",
                    isActive
                      ? "bg-surface-hover/70 text-neon-green border-l-2 border-neon-green"
                      : "text-text-secondary"
                  )}
                >
                  <link.icon className="h-5 w-5 flex-shrink-0" />
                  <AnimatePresence mode="wait">
                    {open && (
                      <motion.div
                        initial={{ opacity: 0, width: 0 }}
                        animate={{ opacity: 1, width: "auto" }}
                        exit={{ opacity: 0, width: 0 }}
                        transition={{ duration: 0.2 }}
                        className="overflow-hidden"
                      >
                        <span className="whitespace-nowrap">{link.label}</span>
                      </motion.div>
                    )}
                  </AnimatePresence>
                </Link>
              </li>
            );
          })}
        </ul>
      </nav>
    </motion.aside>
  );
}


